 <script>
import harvest from "./Popup/harvest";
import knapsackList from "./Popup/knapsackList";
import MButton from "@/components/mButton";
import { mapGetters } from "vuex";
import {BN} from "@/common/util";


 

export default {
  data() {
    return {
      filterFlag: "",
      choiceStatusNft: false,
      harvestNftStatus: false,
      dft:"",
      DCoin:"", 
      defxNFTPoolRecords:[],
      dataHarvest:"",
       reapLoading:false,
       time:null,
    };
  },
  components: { MButton, knapsackList, harvest },
  computed: {
    ...mapGetters(["language"]),
  },
  created() {
    this.rareList();
    this.pendingTokenFn();
    this.pollingFn();
  },
    mounted() {
      this.time =  setInterval(() => {
                this.pollingFn();
      }, 5000)
    },
  methods: {
    // 添加卡牌组件 状态
    addToFn() {
      this.choiceStatusNft = true;
    },
    // 收割组件 状态  
    addToHarvest() {
        this.$api.harvestFn().then(()=>{
           this.pendingTokenFn();
        })    
    },
//  取出状态
    takeAway(item){
      this.dataHarvest = item.nft
      this.harvestNftStatus = true;
    },
    // 确定取出
    closeHarvesNft(id) {
       
      if(id){
           this.reapLoading = true;
            this.$api.unstakingFn(id).then(() =>{
            this.harvestNftStatus = false;
            this.reapLoading = false;
            this.rareList();
      }).catch(() => (this.reapLoading = false));
      }else{
            this.harvestNftStatus = false;
            this.reapLoading = false;
      }
      
    },
    // 子组件方法
    closeDailyNft(id) {
      if (id) {
        this.nftId = id;
        this.$api.externalFn(id).then(()=>{
        this.choiceStatusNft = false;
        this.rareList();
        }); 
      }else{
         this.choiceStatusNft = false;
         // this.$toast.error(this.language.add_failed)
      }
    },
      // 稀有 普通
     rareList(){
       this.$api.defxNftPoolRareList().then((res)=>{
             this.defxNFTPoolRecords = res.defxNFTPoolRecords;
       })  
    },

    // 显示dcion 和 dft的值
     pendingTokenFn(){
          this.$api.pendingToken().then((res)=>{
          this.dft   = new BN(res.pendingToken1).toDisplay('balance').myFixed()
          this.DCoin = new BN(res.pendingToken2).toDisplay('balance').myFixed()
       }) 
     },
      pollingFn(){
        this.rareList();
        this.pendingTokenFn();
      }
  },
   beforeMount() {
      clearInterval(this.time);// 清除定时器
      this.time = null;
  },
};
</script>

