<template>
  <div class="dialog" v-show="choiceStatusNft">
    <div class="dialog-container">
       <div class="dialog-title" @click="goBack"> 
            <img src="../../../images/luck/jian.png" alt="">
               {{language.nftBackpack}}
        </div>

      <div class="knapsack">
        <div v-for="(item, index) in myCardsFiltered" :key="index" @click="getDefineFn(index,item.id,item.resUrl)">
          <img :src="item.resUrl" alt=""  class="img1"/>
          <img src="../../../images/confirm.png" alt=""  class="img2" v-if="statusIndex == index"/> 
          <p>#{{ item.id }}</p> 
        </div>
      </div>




      <div  class="chooseGuard" >* {{language.calculating}}</div>
      <div  class="everyday">* {{language.takenAny}}</div>

      
     <div class="preservation" @click="warrantFn"  v-if="!isWarrant">
        <m-button class="hdr-el-blue" :height="36" style="width: 148px"  :loading="status" >
           {{language.btn_authorize}}
        </m-button>
      </div>


      <div class="preservation" @click="closeFn"  v-else>
        <m-button class="hdr-el-blue" :height="36" style="width: 148px"  :loading="status" >
           {{language.sure}}
        </m-button>
      </div>



   


       

    </div>

    <ul>
      <li v-if="myCardsFiltered.length===0&&!bagLoading" class="no-data flex jc-center ai-center">
          <p class="text fs-16 ta-center">
            <img src="../../../images/no-data.png" alt="nodata"><br>
            {{language.nodata}} NFT
          </p>
        </li>
        <li v-if="bagLoading" class="mask flex jc-center ai-center">
          <i class="icon-loading"></i>
      </li>
    </ul>
    


  </div>
</template>

<script>
import MButton from "@/components/mButton";
import {mapState,mapGetters} from "vuex";
export default {
  components: { MButton },
  props: {
    choiceStatusNft: {
      type: Boolean,
      default: false,
    },

  },
  computed: {
    ...mapState('app',['address']),
    ...mapState('nft', ['myCards']),
    ...mapGetters(['language']),
     myCardsFiltered(){
       
        return this.myCards.filter(c=>c.id.indexOf(this.numText)!==-1)
    },
  },
   watch:{
    address(){
      this.getCardList();
    }
  },
  data() {
    return {
       numText:'',
      list: 31,
      signStatus: [8, 2, 20, 15, 4],
      statusIndex:"",
      id:"",
      url:"",
      status:false,
      isWarrant:"",
    };
  },
  mounted() {
     this.$nextTick(()=>{
        this.getCardList();
        this.IsApprovedFn();
     })
  },
  methods: {
     // 判断是否授权
    IsApprovedFn() {
      this.$api.checkNFTIsApproved("NFTTokenFactory","NFTToken").then((res) => {
        this.isWarrant = Number(res) ;
      });
    },
    // 授权
    warrantFn() {
      this.$api.setNFTApprovalForAll("NFTToken","NFTTokenFactory").then(() => {
        this.isWarrant = true;
      });
    },
      getCardList() {
      if (this.bagLoading) return;
      this.bagLoading = true;
      this.$api.gqlBagNftList(this.address).then(()=>{
        this.bagLoading = false;
      }).catch(()=>{
        this.bagLoading = false;
      });
    },
    // 确定
    closeFn() {
      this.status = true;
      if(this.id){
        this.$emit("closeDailyNft",this.id);
      }else{
        this.$emit("closeDailyNft",this.myCardsFiltered[0].id)
      }   
    },
    // 返回
    goBack(){
         this.$emit("closeDailyNft");
    },
    // 选择头像
    getDefineFn(index,id,url){
          this.statusIndex = index;
          this.id = id;
          this.url = url;
    },
  },
};
</script>
